


















































import Vue from 'vue'
import copy from 'copy-to-clipboard';
import {generateHin, Province} from "@/lib/Validator";

export default Vue.extend({
    name: 'HelloWorld',

    data: () => ({
        snackbar: {
            active: false,
            timeout: 2000,
        },
        count: 10,

        selected: {
            bc: [],
            on: [],
        },

        hins: {
            bc: [],
            on: [],
        },
        provinces: {
            bc: 'British Columbia',
            on: 'Ontario',
        }
    }),

    created() {
        this.generate();
    },

    methods: {
        handleClick(hin: string) {
            copy(hin);
            this.snackbar.active = true;
        },

        generate() {
            this.selected.bc = [];
            this.selected.on = [];

            const bcHins = this.getBC();
            const onHins = this.getON();

            this.hins.bc = bcHins;
            this.hins.on = onHins;
        },

        getBC() {
            const hins = [];

            for (let i = 0; i < this.count; i++) {
                hins.push(generateHin());
            }

            return hins;
        },

        getON() {
            const hins = [];

            for (let i = 0; i < this.count; i++) {
                hins.push(generateHin(Province.ON));
            }

            return hins;
        }
    },

    computed: {
        hinProvinces() {
            return Object.entries(this.provinces);
        }
    }
})
